* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

:root {
  font-size: 62.5%; }

.header {
  height: 100%;
  width: 100%;
  background-image: url("../images/palms.jpg");
  -webkit-filter: opacity(0.85);
  box-shadow: inset 0px 0px 6px 4px grey;
  background-size: cover;
  background-position-y: center;
  padding: 40px 25px; }
  .header-title {
    -webkit-text-stroke: 3px white;
    font-family: "Advent Pro", sans-serif;
    font-size: 8rem;
    padding: 10px 0px 15px 0px 0; }
  .header-form {
    padding: 10px 10px;
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center; }
    .header-form .form-input {
      height: 3rem;
      width: 30rem;
      border-radius: 5px;
      border: solid palevioletred 2px;
      color: black;
      font-family: "Advent Pro", sans-serif;
      font-size: 2rem;
      font-weight: bold; }
    .header-form .form-search {
      height: 3rem;
      width: 10rem;
      border: solid palevioletred 2px;
      border-radius: 5px;
      background-color: white;
      color: palevioletred;
      font-family: "Advent Pro", sans-serif;
      font-size: 2rem;
      font-weight: 600;
      margin: 0 0 0 3px; }
    .header-form .form-reset {
      height: 3rem;
      width: 10rem;
      border: solid palevioletred 2px;
      border-radius: 5px;
      background-color: white;
      color: palevioletred;
      font-family: "Advent Pro", sans-serif;
      font-size: 2rem;
      font-weight: 600;
      margin: 0 0 0 3px; }

.footer {
  height: 100%;
  width: 100%;
  color: bisque;
  padding: 40px 10px;
  font-size: 3rem;
  font-family: "Advent Pro", sans-serif; }

.selected_cards {
  background-color: pink; }

