.body {
  width: 100%;
  background-image: url("../images/nicepalms.jpg");
  box-shadow: inset 0px 0 0px 5px 4px grey;
  background-size: cover;
  background-position-y: bottom; }
  .body .all-lists {
    display: grid;
    grid-template-columns: 1fr;
    width: 80%; }
    @media all and (min-width: 768px) {
      .body .all-lists {
        display: grid;
        grid-template-columns: 1fr 1fr; } }
    .body .all-lists .section {
      display: flex;
      flex-direction: column;
      justify-content: first baseline;
      align-items: center;
      width: 100%; }
      .body .all-lists .section .section-title {
        font-family: "Maven Pro", sans-serif;
        font-size: 4.2rem;
        color: bisque;
        padding: 0 0 1.5rem 0;
        -webkit-text-stroke: 1px lightgrey; }
      .body .all-lists .section .li-card {
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        text-align: center;
        position: relative;
        border: solid bisque;
        margin: 5px 0;
        border-radius: 3px;
        width: 18rem;
        height: 18rem; }
        @media all and (min-width: 768px) {
          .body .all-lists .section .li-card {
            width: 24rem;
            height: 24rem;
            margin: 5px 0;
            border-radius: 3px; } }
        .body .all-lists .section .li-card-title {
          justify-content: center;
          font-family: "Zeyada", cursive;
          font-size: 1.8rem;
          padding: 0.2rem 0 0 0;
          color: white; }
          @media all and (min-width: 768px) {
            .body .all-lists .section .li-card-title {
              font-size: 2.5rem;
              padding: 0.5rem 0 0 0; } }
        .body .all-lists .section .li-card-img {
          border-radius: 8px;
          border: grey solid 1px;
          margin: 1rem 0 0 0;
          grid-column-start: 1;
          width: 120px;
          height: 120px; }
          @media all and (min-width: 768px) {
            .body .all-lists .section .li-card-img {
              width: 150px;
              height: 150px; } }
        .body .all-lists .section .li-card-icon {
          font-size: 1.5rem;
          position: absolute;
          margin: 2px 5px 0 0;
          right: 1.5px;
          top: 10px;
          color: bisque;
          font-weight: bolder; }
          @media all and (min-width: 768px) {
            .body .all-lists .section .li-card-icon {
              font-size: 2.6rem;
              position: absolute;
              margin: 2px 5px 0 0;
              -webkit-text-stroke: 1px palevioletred; } }
